<template>
  <span class="chatConnect" v-html="$t('enomix.toast')"></span>
</template>

<script>
export default {
  name: 'ChatLoading',
  mounted() {
    this.$uiHandler.consulting.chatConOff();
  },
  computed: {
    showConsultingDelayToast() {
      return this.$store.state.enomix.visibleToast;
    },
  },
  watch: {
    showConsultingDelayToast(currentValue) {
      if (currentValue) {
        setTimeout(() => {
          if (!this.showConsultingDelayToast) {
            return;
          }

          this.$uiHandler.consulting.chatConOn();
        }, 500);
      } else {
        this.$uiHandler.consulting.chatConOff();
      }
    },
  },
};
</script>

